.notVisible {
  display: none;
}

form {
  background: #fff;
  border-radius: 8px;
  flex-direction: column;
  gap: 12px;
  width: 100%;
  max-width: 400px;
  padding: 20px;
  display: flex;
}

label {
  color: #333;
  font-weight: 600;
}

input[type="text"] {
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  padding: 10px;
  font-size: 16px;
  transition: border-color .2s;
}

input[type="text"]:focus {
  border-color: #999;
  outline: none;
}

input[type="submit"] {
  color: #fff;
  cursor: pointer;
  background: gray;
  border: 0;
  border-radius: 8px;
  width: 100%;
  margin-top: 4px;
  padding: 10px;
  font-size: 1rem;
}

input[type="submit"]:hover {
  filter: brightness(.95);
}
